<template>
  <div>
    <v-navigation-drawer v-model="drawer" clipped width="300px" permanent class="framecontent">
      <v-toolbar dense>
        Узлы учета
        <v-spacer></v-spacer>

        <!-- <div class="text-xs-center"> -->
        <v-menu offset-y>
          <template v-slot:activator="{ on }">
            <v-btn flat small color="primary" v-on="on">Добавить УУ</v-btn>
          </template>
          <v-list>
            <v-list-tile v-for="(item, index) in items" :key="index" @click="item.click">
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
        <!-- </div> -->
      </v-toolbar>

      <v-list two-line subheader>
        <v-list-tile v-for="uzel in uzels_list" :key="uzel.id" avatar @click="openUU(uzel)">
          <v-list-tile-content>
            <v-list-tile-title>{{ uzel.name }}</v-list-tile-title>
            <v-list-tile-sub-title
              class="sub"
            >{{ uzel.id}} , {{ uzel.q }} Гкал/ч, {{ uzel.t1 }}/{{ uzel.t2 }}°C, {{ uzel.p1 }}/{{ uzel.p2 }} мвст</v-list-tile-sub-title>
          </v-list-tile-content>

          <v-list-tile-action>
            <v-btn icon ripple>
              <v-icon color="grey lighten-1">info</v-icon>
            </v-btn>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <!-- <div class="framecontent"></div> -->
    <div v-if="showCoForm">
      <co-form v-if="showCoForm" :ssps="eeee" :tpf="tpf"></co-form>
    </div>

    <!-- <vent-form></vent-form> -->
  </div>
</template>
<script>
import CoForm from "@/components/CoForm.vue";
import VentForm from "@/components/VentForm.vue";
// import CocoForm from "@/components/UuCoForm.vue";
import { mapState } from "vuex";
export default {
  components: {
    CoForm,
    VentForm
    // CocoForm
  },
  data() {
    return {
      drawer: true,
      eeee: {},
      tpf: "",
      items: [
        {
          icon: "view_list",
          title: "отопление",
          click: () => {
            this.$store.dispatch("change_showCoForm");
            class Usel {
              constructor(name) {
                this.name = "Узел учёта отопления";
                this.tipe = "showCoForm";
                (this.q = 0.25111),
                  (this.t1 = 95),
                  (this.t2 = 70),
                  (this.p1 = 65),
                  (this.p2 = 55),
                  (this.sx_pr = 0),
                  (this.fu = 0);
              }
            }
            this.eeee = new Usel();
            this.tpf = 0;
          }
        },
        {
          icon: "view_list",
          title: "вентиляция",
          click: () => {
            // this.eeee = this.start_uu_vent;
            // this.$store.dispatch("change_showStartVentForm");
          }
        },
        {
          icon: "chrome_reader_mode",
          title: "ГВС",
          click: () => {
            // this.eeee = this.start_uu_co;
            // this.$store.dispatch("change_showStartCoForm");
          }
        }
      ]
    };
  },
  computed: {
    ...mapState({
      showCoForm: state => state.isxx.showCoForm,
      uzels_list: state => state.RasHods.uzels_list,
      uzels: state => state.RasHods.uzels
    })
  },

  methods: {
    openUU(uzel) {
      // this.eeee = this.uzels[uzel.id];
      const qqq = this.uzels[uzel.id];
      this.eeee = qqq;
      this.tpf = 1;
      this.$store.dispatch(`change_${uzel.tipe}`);
    }
  }
};
</script>
<style scoped>
.framecontent {
  height: 100vh;
  overflow-y: hidden;
}
.sub {
  font-size: 0.8em;
  font-style: italic;
}
.custom-loader {
  animation: loader 1s infinite;
  display: flex;
}
@-moz-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>