export default {
	state: {
		uzels: {},
		uzels_list: []
	},

	getters: {
		GET_UZELS: (state) => state.uzels
	},

	actions: {
		REMOVE_UCO(context, payload) {
			context.commit('MU_REMOVE_UU', payload);
		}
	},

	mutations: {
		MU_REMOVE_UU(state, payload) {
			delete this.state.RasHods.uzels[payload];
			let _e = this.state.RasHods.uzels_list;

			_e.forEach(function(el1) {
				if (el1.id == payload) {
					_e.splice(_e.indexOf(el1), 1);
				}
			});
			this.state.RasHods.uzels_list = _e;
		}
	}
};
