export default {
	state: {
		start: {
			name: 'Узел учёта отопления _blank',
			tipe: 'showCoForm',
			q: 0.25111,
			t1: 95,
			t2: 70,
			p1: 65,
			p2: 55,
			sx_pr: 0,
			fu: 0
		},
		count: 0
	},
	getters: {},
	actions: {
		ADD_UCO(context, payload) {
			let _id = '';
			context.commit('incrementCo');
			let ee = this.state.IsxCo.count;
			_id = `uselCo_${ee}`;
			payload.id = _id;
			let pay = {
				name: _id,
				isx: payload
			};
			context.commit('MU_ADD_UU', pay);
		}
	},
	mutations: {
		qq(state, payload) {
			this.state.IsxCo.temp_uu_co = payload;
		},
		incrementCo(state) {
			this.state.IsxCo.count++;
		}
	}
};
