export default {
	state: {
		uu_vent: {
			id: '',
			name: 'Узел учёта вентиляции',
			tipe: 'showVentForm',
			Q: 0.45,
			t1: 130,
			t2: 70,
			p1: 65,
			p2: 55,
			fu: 0
		},
		count: 0
	},
	getters: {
		GET_UU_VENT: (state) => state.uu_vent
	},
	actions: {
		ADD_UVENT(context) {
			const _spec = this.state.IsxVent.uu_vent;
			context.commit('incrementVent');
			let ee = this.state.IsxVent.count;
			let _id = `uselVent_${ee}`;
			context.commit('id_uu_vent', _id);
			const pay = {
				name: _id,
				isx: _spec
			};
			context.commit('MU_ADD_UU', pay);
		}
	},
	mutations: {
		incrementVent(state) {
			this.state.IsxVent.count++;
		},
		id_uu_vent(state, payload) {
			this.state.IsxVent.uu_vent.id = payload;
		}
	}
};
