import Vue from 'vue';
import Vuex from 'vuex';
import calcModule from '@/store/calc';
import IsxCo from '@/store/IsxCo';
import IsxVent from '@/store/IsxVent';
import RasHods from '@/store/rashods';
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		isxx: {
			showStartCoForm: false,
			showStartVentForm: false,
			showCoForm: false,
			showVentForm: false
		}
	},
	modules: {
		calcModule,
		IsxCo,
		IsxVent,
		RasHods
	},
	getters: {
		getshowStartCoForm: (state) => state.isxx.showStartCoForm,
		getshowStartVentForm: (state) => state.isxx.showStartVentForm,
		getshowCoForm: (state) => state.isxx.showStartCoForm,
		getshowVentForm: (state) => state.isxx.showStartVentForm
	},
	mutations: {
		mu_showStartCoForm(state) {
			state.isxx.showStartCoForm = !state.isxx.showStartCoForm;
		},
		mu_showStartVentForm(state) {
			state.isxx.showStartVentForm = !state.isxx.showStartVentForm;
		},
		mu_showCoForm(state) {
			state.isxx.showCoForm = !state.isxx.showCoForm;
		},
		mu_showVentForm(state) {
			state.isxx.showVentForm = !state.isxx.showVentForm;
		},
		MU_ADD_UU(state, payload) {
			let rw = payload.name;
			this.state.RasHods.uzels[rw] = payload.isx;
			this.state.RasHods.uzels_list.push(payload.isx);
		}
	},
	actions: {
		change_showStartCoForm(context) {
			context.commit('mu_showStartCoForm');
		},
		change_showStartVentForm(context) {
			context.commit('mu_showStartVentForm');
		},
		change_showCoForm(context) {
			context.commit('mu_showCoForm');
		},
		change_showVentForm(context) {
			context.commit('mu_showVentForm');
		}
	}
});
